@import '_styles/variables';
@import '_styles/mixins';

.LinksRoute {
  text-align: center;
  padding-top: 0;

  &__avatar {
    display: inline-block;
    width: 80px;
    border-radius: 50%;
    overflow: hidden;
  }

  &__logo {
    width: 72px;
    fill: currentColor;
    opacity: 0.5;
    margin: 10px 0;
  }

  &__smLinks {
    margin-bottom: 20px;
  }

  &__smLinkItem {
    display: inline-block;
    width: 36px;
    height: 36px;
    text-align: center;
    line-height: 36px;
    background-color: rgba($color__default, 0.1);
    border-radius: 2px;
    margin: 0 10px;
  }

  &__smLinkIcon {
    width: 1em;
    fill: currentColor;
    vertical-align: middle;
  }

  &__grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-auto-rows: 120px;
    column-gap: 4px;
    row-gap: 4px;

    @include respondAbove(s) {
      grid-template-columns: 1fr 1fr;
      grid-auto-rows: 160px;
    }
  }

  &__gridItem {
    position: relative;
    color: #fff;
    background: transparent no-repeat center;
    background-size: cover;
  }

  &__gridItemContent {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    // flex: 1;
    // height: 100%;
    justify-content: center;
    align-items: center;
    line-height: 1.5;
    padding: 16px;
    backdrop-filter: blur(12px);
    background-color: rgba(0,0,0,0.2);
    text-shadow: 0px 2px 6px rgba(0,0,0,0.2);
  }

  &__gridItemTitle {

  }

  &__gridItemCategory {
    @include fontCaps;
    font-size: 0.625em;
    margin-bottom: 0.75em;
  }

}
